:root {
    --name-color: #343E3D;
    --link-non-hover: gray;
    --link-hover: #1CE8AB;
}


.container__student{
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: row;
        flex-direction: row;
    /* flex-flow: row wrap; */
    -ms-flex-pack: space-evenly;
        justify-content: space-evenly;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
    font-family: 'Roboto Condensed', sans-serif;
}

.card__student {
    width: 25%;
    height: 25%;
    margin: 1%;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-flow: column;
        flex-flow: column;
    -ms-flex-pack: space-evenly;
        justify-content: space-evenly;
    -ms-flex-align: center;
        align-items: center;
    /* border: 2px black solid; */
    border-radius: 5px;
    -webkit-transition: all .5s ease-in-out;
    -o-transition: all .5s ease-in-out;
    transition: all .5s ease-in-out;
}

.student__img {
    height: 20%;
    width: 20px;
    border-radius: 4px;
}

.student__links {
    font-size: 20px;
    margin: 3px;
    color: var(--link-non-hover);
    -webkit-transition: all .5 ease-in-out;
    -o-transition: all .5 ease-in-out;
    transition: all .5 ease-in-out
}
.student__links:hover {
    color: var(--link-hover);
    -webkit-transform: scale(2.2);
        -ms-transform: scale(2.2);
            transform: scale(2.2);
}

.student__name {
    font-size: 25px;
}

.card__student img {
    height: 65%;
    width: 65%;
}



