
.student__modal {
    position: fixed;
    height: 100%;
    width: 100%;
    /* top: 0; */
    /* left: 0; */
    background-color: rgba(0, 0, 0, .5);
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
        -ms-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    max-height: 100%;
}

.student__modal__content {
    border-radius: 4px;
    width: 95%;
    max-width: 1000px;
    position: absolute;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
        -ms-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    background: #fff;
    padding-bottom: 100px;
}

.student__modal__content img {
    width: 250px;
    height: auto;
    border-radius: 4px;
}

.student__container {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: space-evenly;
        justify-content: space-evenly;
    -ms-flex-direction: row;
        flex-direction: row;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
    -ms-flex-align: center;
        align-items: center;
}
.student__middle {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-pack: space-evenly;
        justify-content: space-evenly;
    height: 160px;
}
.student__right {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    height: 160px;
}

.student__right > p {
    width: 500px;
}



.student__links {
    font-size: 20px;
    margin: 3px;
    color: var(--link-non-hover);
    -webkit-transition: all .5 ease-in-out;
    -o-transition: all .5 ease-in-out;
    transition: all .5 ease-in-out
}
.student__links:hover {
    color: var(--link-hover);
    -webkit-transform: scale(2.2);
        -ms-transform: scale(2.2);
            transform: scale(2.2);
}

.student__close {
    color: gray;
    float:right;
    margin: 3px 5px 0 0;
}

@media only screen and (max-width: 415px) {
    .student__modal__content {
        border-radius: 4px;
        width: 75%;
        height: 85%;
        max-width: 1000px;
        position: absolute;
        top: 50%;
        left: 50%;
        -webkit-transform: translate(-50%, -50%);
            -ms-transform: translate(-50%, -50%);
                transform: translate(-50%, -50%);
        background: #fff;
        margin-top: 16px;
        margin-bottom: 16px;
        padding: 3%;
    }
    .student__modal__content img {
        width: 100px;
        height: auto;
        border-radius: 4px;
    }
    .student__middle {
        display: -ms-flexbox;
        display: flex;
        -ms-flex-direction: row;
            flex-direction: row;
        -ms-flex-pack: space-evenly;
            justify-content: space-evenly;
        /* align-items: ; */
        /* margin-bottom: 10%; */
    }
    .student__right {
        display: -ms-flexbox;
        display: flex;
        -ms-flex-direction: column;
            flex-direction: column;
        width: 300px;
        overflow: auto scroll;
    }
    
    .student__right > p {
        width: 75%;
    }
    
}


